/* ============================================
   Hype 섹션 전체 레이아웃
   ============================================ */

/* 섹션 전체 컨테이너 */
.hype-section {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 120px 0;
    background-color: transparent;
    position: relative;
    overflow: hidden;
}

/* 콘텐츠 중앙 정렬 컨테이너 */
.hype-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 64px;
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 40px;
}


/* ============================================
   좌측 텍스트 영역
   ============================================ */

/* 텍스트 그룹 컨테이너 */
.hype-text {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 415px;
    height: 540px;
    position: relative;
    flex-shrink: 0;
}

/* 영어 제목 */
.headline {
    font-family: 'Roboto', sans-serif;
    font-size: 96px;
    font-weight: 700;
    color: var(--text-color);
    letter-spacing: -1.92px;
    line-height: 1.1;
    margin-top: 0;
    margin-bottom: 40px;
    text-shadow: 0px 7px 10px var(--accent-shadow-hype);
}

/* 한글 설명 텍스트 */
.desc {
    font-family: 'Inter', sans-serif;
    font-size: 24px;
    color: var(--sub-text-color);
    line-height: 1.6;
    margin-bottom: 32px;
}


/* ============================================
   우측 이미지 영역
   ============================================ */

/* 이미지 래퍼 */
.hype-image-wrapper {
    width: 737px;
    height: 931px;
    flex-shrink: 0;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

/* 이미지 */
.hype-image-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}
